.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;
  color: $secondary-color;
  overflow: scroll;

  @include on-desktop {
    overflow: hidden;
  }

  &__top {
    margin-bottom: 24px;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__content {
    @include constent-padding-inline;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: inherit;
    text-decoration: none;
  }

  &__book-test-drive {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $secondary-color;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-decoration: none;
    color: inherit;
  }
}
